<template>
  <div id="cart">
    <!-- 顶部固定 -->
    <div class="cart_po">
      <!-- <div class="cart_top">
        <span><i class="iconfont icon-dui"></i>100%正品保证</span>
        <span><i class="iconfont icon-dui"></i>所有商品精挑细选</span>
        <span><i class="iconfont icon-dui"></i>售后无忧</span>
      </div> -->
      <!-- 购物数量 -->
      <div class="cart_num">
        <div v-if="list.length">
          <p>
            购物数量：<span>{{ num }}</span>
          </p>
          <button @click="icr">
            <span v-if="gl">管理</span><span v-else>取消</span>
          </button>
        </div>
      </div>
    </div>
    <!-- 购物车列表 -->
    <div class="cart_list" v-if="list.length">
      <van-checkbox-group
        v-model="result"
        ref="checkboxGroup"
        checked-color="#e93323"
      >
        <div class="cart_list_item" v-for="(i, index) in list" :key="i.id">
          <!-- 左 -->
          <div class="cart_list_item_l">
            <van-checkbox :name="index" />
          </div>
          <!-- 中图 -->
          <div class="cart_list_item_c" @click="tzs(i)">
            <!-- <van-image :src="i.productInfo.image" fit="cover" /> -->
            <img :src="i.productInfo.image" />
          </div>
          <!-- 右 -->
          <div class="cart_list_item_r">
            <div class="van-ellipsis">{{ i.productInfo.store_name }}</div>
            <p>属性：{{ i.productInfo.attrInfo.suk }}</p>
            <div>
              <span>￥{{ i.truePrice }}</span>
              <div>
                <!--v-model="value[index]" -->
                <van-stepper
                  min="1"
                  :max="i.trueStock"
                  input-width="1.4rem"
                  v-model="value[index]"
                  button-size="0.58rem"
                />
              </div>
            </div>
          </div>
        </div>
      </van-checkbox-group>
    </div>
    <!-- 失效商品 -->
    <div class="invalid" v-if="invalid.length">
      <div class="invalid_t">
        <van-collapse v-model="activeNames">
          <van-collapse-item name="1" ref="item">
            <template #title>
              <div class="clear" @click="change">
                <p @click="dels"><i class="iconfont icon-shanchu"></i>清空</p>
              </div>
            </template>
            <template #right-icon>
              <p><van-icon name="arrow" />失效商品</p>
            </template>
            <div class="lnvalid_goods">
              <!-- <Lnvalid
                v-for="i in invalid"
                :key="i.id"
                :item="i.productInfo"
              ></Lnvalid> -->
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>
    <!-- 底部全选 -->
    <div class="footer" v-if="list.length">
      <div class="footer_l">
        <!-- {{ nums }} -->
        <van-checkbox v-model="checkeds" checked-color="#e93323" @click="gg"
          >全选<span v-if="result.length">（{{ nums }}）</span></van-checkbox
        >
      </div>
      <!-- 下单 -->
      <div class="footer_r" v-if="gl">
        <span class="total_price"
          >￥<span v-if="result.length">{{ totalPrice }}</span
          ><span v-if="!result.length">0</span></span
        >
        <div class="orders" @click="xd">立即下单</div>
      </div>
      <!-- 取消，收藏 -->
      <div class="footer_r" v-else>
        <div class="btn_item sc" @click="collect">收藏</div>
        <div class="btn_item dels" @click="erase">删除</div>
      </div>
    </div>
    <!-- 购物车没有商品时，出现推荐商品 -->
    <div class="cart_referral" v-if="!list.length">
      <!-- 提示没有商品 -->
      <div class="cart_none">
        <img src="http://47.115.51.185/h5/img/noCart.6f2abfef.png" />
      </div>
      <!-- 推荐开始 -->
      <div class="cart_tj">
        <!-- <SearchNone :arr="referralArr"></SearchNone> -->
      </div>
    </div>
    <!-- loading -->
    <van-loading size="64px" vertical v-if="flag">加载中...</van-loading>
  </div>
</template>
<script>
// import SearchNone from "../components/search/SearchNone.vue";
// 失效商品
// import Lnvalid from "../components/cart/Lnvalid.vue";
export default {
  components: {
    // SearchNone,
    // Lnvalid,
  },
  data() {
    return {
      // 购物车商品数量
      num: 0,
      // 购物车商品列表
      list: [],
      // 失效商品列表
      invalid: [],
      // 推荐商品列表
      referralArr: [],
      // 全选，反选
      result: [],
      checked: true,
      activeNames: ["1"],
      // 底部全选
      checkeds: false,
      // 计步器
      value: [],
      // 管理的点击
      // true表示显示管理，false表示显示取消
      gl: true,
      // loading加载
      flag:true,
    };
  },
  created() {
    this.init();
  },
  mounted() {
    if (this.checked) {
      this.$nextTick(() => {
        if (this.$refs.checkboxGroup) {
          this.$refs.checkboxGroup.toggleAll(true);
        }
      });
    }
  },
  methods: {
    // 让点击失效商品的右侧，不能开启失效商品详情
    change() {
      let node = this.$refs.item;
      node.toggle();
    },
    // 清除失效商品
    dels() {
      // ids是失效列表每一个的ids的数组集合,使用map
      // let ids =
      // this.axios.post("/api/cart/del",{
      //   ids:ids
      // }).then(res=>{
      //   console.log(res);
      // })
    },
    // 点击全选按钮
    gg() {
      if (this.checkeds) {
        this.$refs.checkboxGroup.toggleAll(true);
      } else {
        this.$refs.checkboxGroup.toggleAll(false);
      }
    },
    // 底部提交订单
    xd() {
      let cartId = this.result
        .map((item) => {
          return this.list[item];
        })
        .map((item) => {
          return item.id;
        });
      // console.log(cartId.join(","));
      this.$router.push("/order/submit/" + cartId.join(","));
    },
    // 管理按钮的点击切换
    icr() {
      let f = this.gl;
      this.gl = !f;
    },
    // 点击收藏
    collect() {
      let id = this.result.map((item) => {
        return this.list[item].productInfo.id;
      });
      this.axios
        .post("http://47.115.51.185/api/collect/all", {
          category: "product",
          id: id,
        })
        .then(() => {
          this.$toast({
            type: "success",
            message: "收藏成功",
            duration: 400,
          });
        });
    },
    // 点击删除
    erase() {
      console.log("aa");
      let ids = this.result.map((item) => {
        return this.list[item].id;
      });
      this.axios
        .post("http://47.115.51.185/api/cart/del", {
          ids: ids,
        })
        .then(() => {
          this.init();
        });
    },
    // 点击购物车图片跳转到相应的商品详情
    tzs(val){
      let id = val.product_id;
      this.$router.push(`/detail/${id}`);
    },
    // 初始化请求
    init() {
      this.flag = true;
      // 购物车里面的商品数量
      this.axios("http://47.115.51.185/api/cart/count").then((res) => {
        console.log(res);
        this.num = res.data.data.count;
        // this.flag=false;
      });
      // 购物车商品列表
      this.axios.get("http://47.115.51.185/api/cart/list").then((res) => {
        console.log("购物车商品列表==>", res.data.data);
        let data = res.data.data;
        // 购物车商品列表
        this.list = data.valid;
        // this.flag=false;
        // 每个商品在购物车的数量
        this.value = this.list.map((item) => {
          return item.cart_num;
        });
        // console.log(this.list);
        // 失效商品列表
        this.invalid = data.invalid;
        // console.log(this.invalid);
      });
      // 推荐商品
      this.axios.get("http://47.115.51.185/api/product/hot?page=1&limit=20").then((res) => {
        this.referralArr = res.data.data;
        this.flag=false;
        console.log(this.referralArr);
      });
    },
  },
  watch: {
    result(news) {
      // console.log(news);
      // console.log(this.$refs.checkboxGroup);
      if (news.length == this.list.length) {
        this.checkeds = true;
      } else {
        this.checkeds = false;
      }
    },
  },
  computed: {
    // 总价计算
    totalPrice() {
      return this.result
        .map((item) => {
          return this.list[item].truePrice * this.value[item];
        })
        .reduce((prev, curr) => {
          return prev + curr;
        });
    },
    // // 全选的数量
    nums() {
      return this.result
        .map((item) => {
          return this.value[item];
        })
        .reduce((prev, curr) => {
          return prev + curr;
        });
    },
  },
  // updated() {},
};
</script>
<style lang="less">
#cart {
  width: 100vw;
  background: #f5f5f5 !important;
  box-sizing: border-box;
  padding-top: 1.75rem;
  .cart_po {
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    height: 1.75rem;
    z-index: 9;
    .cart_top {
      padding: 0.3rem;
      height: 0.76rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      font-size: 0.3rem;
      background-color: #f5f5f5;
      color: #8c8c8c;
      span {
        i {
          font-size: 0.22rem;
          margin-right: 0.1rem;
        }
      }
    }
    // 顶部结束
    // 购物数量
    .cart_num {
      width: 100vw;
      box-sizing: border-box;
      padding: 0.3rem;
      font-size: 0.28rem;
      color: #282828;
      background-color: #fff;
      text-align: left;
      div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        span {
          color: #fc4141 !important;
        }
        button {
          background-color: transparent;
          border: 1px solid #282828;
          span {
            color: #282828 !important;
          }
        }
      }
    }
  }
  //有效购物商品
  .cart_list {
    .van-checkbox-group {
      padding-bottom: 90px;
      .cart_list_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100vw;
        box-sizing: border-box;
        padding: 0.25rem 0.3rem;
        background-color: #fff;
        margin-bottom: 0.15rem;
        position: relative;
        margin-top: 0.2rem;
        .cart_list_item_l {
          width: 0.8rem;
        }
        // 图片
        .cart_list_item_c {
          width: 2.28rem;
          height: 2.28rem;
          border-radius: 0.08rem;
          overflow: hidden;
          img {
            width: 100%;
            height: auto;
          }
        }
        // 右
        .cart_list_item_r {
          margin-left: 0.3rem;
          width: calc(10rem - 2.28rem - 0.8rem - 0.9rem);
          height: 2.28rem;
          box-sizing: border-box;
          padding: 0.08rem 0;
          font-size: 0.28rem;
          color: #282828;
          text-align: left;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          p {
            font-size: 0.24rem;
            color: #868686;
          }
          div:nth-child(3) {
            font-size: 0.32rem;
            color: #282828;
            display: flex;
            align-items: center;
            justify-content: space-between;
            // 1px solid #a4a4a4
            .van-stepper {
              display: flex;
              justify-content: center;
              button,
              input {
                background-color: transparent;
                border: 1px solid #a4a4a4;
              }
              .van-stepper__input {
                margin: 0;
              }
              button {
                font-size: 0.28rem;
                color: #a4a4a4;
              }
              input {
                font-size: 0.28rem;
                color: #282828;
              }
              button:nth-child(1) {
                border-right: none;
                border-radius: 0.03rem 0 0 0.03rem;
              }
              button:nth-child(3) {
                border-left: none;
                border-radius: 0 0.03rem 0.03rem 0;
              }
            }
          }
        }
      }
    }
  }
  // 无效购物商品
  .invalid {
    .invalid_t {
      .van-collapse {
        .van-collapse-item {
          // 标题
          .van-cell {
            display: flex;
            flex-direction: row-reverse;
            font-size: 0.28rem;
            color: #282828;
            // 左侧
            p {
              display: flex;
              align-items: center;
              i {
                margin-right: 0.14rem;
              }
            }
            // 右侧
            .van-cell__title {
              text-align: right;
              font-size: 0.26rem;
              color: #999;
              p {
                text-align: right;
                display: flex;
                justify-content: flex-end;
              }
              i {
                margin-right: 0.14rem;
              }
            }
          }
          // 内容
          .van-collapse-item__wrapper {
          }
        }
      }
    }
  }
  // 底部全选
  .footer {
    width: 100vw;
    height: 1.2rem;
    box-sizing: border-box;
    padding: 0 0.3rem;
    background-color: #fafafa;
    border-top: 1px solid #eee;
    position: fixed;
    left: 0;
    bottom: 1.33333rem;
    z-index: 99;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .footer_l {
      .van-checkbox {
        // 文字
        .van-checkbox__label {
          font-size: 0.32rem;
          color: #282828;
          margin-left: 0.3rem;
        }
      }
    }
    // 右
    .footer_r {
      font-size: 0.3rem;
      display: flex;
      align-items: center;
      span {
        color: #fc4141 !important;
        font-size: 0.34rem;
      }
      div {
        color: #fff;
        font-size: 0.3rem;
        width: 2.66rem;
        height: 0.86rem;
        border-radius: 0.5rem;
        text-align: center;
        line-height: 0.86rem;
        margin-left: 0.22rem;
        background-color: #e93323;
      }
      // 删除和收藏按钮
      .btn_item {
        font-size: 0.32rem;
        color: #999;
        border-radius: 0.5rem;
        border: 1px solid #999;
        width: 1.8rem;
        height: 0.86rem;
        text-align: center;
        line-height: 0.86rem;
        background-color: transparent;
      }
      .sc {
        color: #ff3700 !important;
        border: 1px solid #ff3700 !important;
      }
    }
  }
  // 商品推荐
  .cart_referral {
    width: 100vw;
    margin-top: 0.2rem;
    box-sizing: border-box;
    padding: 0.5rem 0.3rem 0.3rem;
    background: #fff;
    .cart_none {
      width: 4.14rem;
      height: 3.36rem;
      margin: 0 auto;
      img {
        width: 100%;
        height: auto;
      }
    }
    // 推荐开始
    .cart_tj {
      margin-top: 0.4rem;
    }
  }
  // loading
  .van-loading{
    width: 100vw;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
}
</style>